import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { FormConfig } from "semantica-dynamic-form-generator/src/form.model";

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css'],
  encapsulation: ViewEncapsulation.None
})
export class AppComponent implements OnInit {
  pages: any;
  selectedTab: number = 0;
  pageCount: number = 0;

  ngOnInit(){
    let formConfig = new FormConfig(this.getConfig());
    this.pages = formConfig.pages;
    this.pageCount = this.pages.length;
  }

  getConfig(): any{
    let config = {
      "formId": '123456Test',
      "form": 'animals',
      "type": 'lion',
      "version": 'v1',
      "pages":[
        {
          "title": "Basic",
          "groups": [
            {
              "orientation": 1,
              "fields": [
                {
                  "fieldName": 'firstName',
                  "placeholder": 'First name',
                  "type": 'text'
                },
                {
                  "fieldName": 'lastName',
                  "placeholder": 'Last name',
                  "type": 'text'
                },
                {
                  "fieldName": 'birthDate',
                  "placeholder": 'Birthdate',
                  "type": 'date'
                }
              ]
            },
            {
              "orientation": 0,
              "fields": [
                {
                  "fieldName": 'hobbies',
                  "placeholder": 'Hobbies',
                  "type": 'select',
                  "class": "three",
                  "options": [
                    {
                      "key": '1',
                      "value": 'Football'
                    },
                    {
                      "key": '2',
                      "value": 'Baseball'
                    }
                  ]
                },
                {
                  "fieldName": 'other',
                  "placeholder": 'Other hobbies',
                  "class": "twelve",
                  "type": 'text'
                }
              ]
            },
            {
              "orientation": 1,
              "fields": [
                {
                  "fieldName": 'nextbutton',
                  "placeholder": 'Next',
                  "class": "two",
                  "type": "button"
                },
                {
                  "fieldName": 'clearButton',
                  "placeholder": 'Clear',
                  "type": "reset"
                }
              ]
            }
          ]
        },
        {
          "title": "Extra",
          "groups": [
            {
              "orientation": 0,
              "fields": [
                {
                  "fieldName": 'age',
                  "placeholder": "Age",
                  "class": "two",
                  "type": 'number'
                },
                {
                  "fieldName": 'height',
                  "placeholder": "Height",
                  "class": "two",
                  "type": 'number'
                },
                {
                  "fieldName": 'weight',
                  "placeholder": "Weight",
                  "class": "two",
                  "type": 'number'
                }
              ]
            },
            {
              "orientation": 1,
              "fields": [
                {
                  "fieldName": 'clearButton',
                  "placeholder": 'Clear',
                  "type": "reset"
                }
              ]
            }
          ]
        }
      ]
    }

    return config;
  }

  doNext(): void{
    console.log('old : ' + this.selectedTab);
    if(this.selectedTab <= this.pageCount){
      this.selectedTab += 1;
    }
    console.log('new : ' +this.selectedTab);
  }
}



